import React, { useRef } from 'react'
import { Helmet } from 'react-helmet-async'
import { useNavigate, useParams } from 'react-router'

import ArrowBackIcon from '@mui/icons-material/ArrowBack'
import { Box, Button, Card, CardHeader, Container, Divider } from '@mui/material'
import type { Editor } from '@toast-ui/react-editor'

import CancelSubmitBtns from 'src/components/CancelSubmitBtns'
import TextEditor from 'src/components/TextEditor'

import { useGetConferenceRound, usePatchConferenceRound } from 'src/hooks/api/clubConferenceHooks'

const MeetingNotes = () => {
  const navigate = useNavigate()
  const handleClickNavigateBack: React.MouseEventHandler<HTMLButtonElement> = () => {
    navigate(-1)
  }
  const editorRef = useRef<Editor>(null)
  const { id, confId, meetingId } = useParams()
  const clubId = Number(id)
  const conferenceId = Number(confId)
  const conferenceRoundId = Number(meetingId)
  const { data, isSuccess } = useGetConferenceRound(clubId, conferenceId, conferenceRoundId)

  const patchRoundMutation = usePatchConferenceRound()
  const handleSubmit = () => {
    const content = editorRef.current?.getInstance().getHTML() || ''
    isSuccess
      ? patchRoundMutation.mutate({ clubId, conferenceId, conferenceRoundId, note: content, startAt: data?.startAt })
      : null
    navigate(-1)
  }
  return (
    <Box m={3}>
      <Helmet>
        <title>회의록 작성</title>
      </Helmet>
      <Container>
        <Card>
          <Box sx={{ display: 'flex', flexDirection: 'row', alignItems: 'center', p: 1 }}>
            <Button variant="text" onClick={handleClickNavigateBack} color="primary">
              <ArrowBackIcon />
            </Button>
            <CardHeader title={`${data?.round} 회차 회의록`} />
          </Box>
          <Divider />
          {isSuccess && (
            <TextEditor
              initialValue={data.note}
              editorRef={editorRef}
              toolbarItems={[
                ['heading', 'bold', 'italic', 'strike'],
                ['hr', 'quote'],
                ['ul', 'ol', 'task'],
                ['table', 'image', 'link'],
                ['code', 'codeblock']
              ]}
            />
          )}
        </Card>
        <CancelSubmitBtns handleSubmit={handleSubmit} submitBtnText="등록 / 수정" />
      </Container>
    </Box>
  )
}

export default MeetingNotes
